(function(factory) {

    "use strict";

    if (typeof define === 'function' && define['amd']) {
        define([], factory);
    }
    else {
        window.lski = (window.lski || {});
        window.lski.alerts = factory();
    }

})(function() {

    var alerts = {
        alert: function(mess, cb) {

            setTimeout(function() {
                window.alert(mess);
                if (typeof cb === 'function') {
                    cb();
                }
            }, 1);
        },
        confirm: function(mess, cb) {

            setTimeout(function() {
                cb(window.confirm(mess));
            }, 1);
        }
    };

    return alerts;
});